.formulario {

    p{
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;
    }

    legend {
        font-size: 2rem;
        color: $gris;
    }

    label {
        display: block;
        font-weight: $bold;
        text-transform: uppercase;
    }

    input:not([type="submit"]),
    select {
        padding: 1rem;
        display: block;
        background-color: white;
        width: 100%;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    .forma-contacto {
        display: flex;
        max-width: 60rem;
        align-items: center;
        justify-content: space-between;
    }

    input[type="checkbox"] {
        margin: 0;
        width: auto;
    }
}