html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,*::before,*::after {
  box-sizing: inherit;
}

body {
  font-family: $fuente_principal;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: $negro;
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img,picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: $fuente_principal;
  margin: 2.5rem 0;
  font-weight: $regular;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}