.datos {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    thead {
        background-color: $gris;
        th {
            color: white;
            padding: 1rem;
        }
    }
    tbody {
        td {
            text-align: center;
            height: 1rem;
            p {
                margin: 1rem 0 0 0;
                font-size: 1.6rem;
            }
        }
    }
}