.header {
    background-color: $blanco;
    padding: 1rem 0 1rem 0;
}

.contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .logo {
        width: 9rem;
    }
    @include desktop {
        flex-direction: row;
        gap: 2rem;
    }

    h1 {
        font-weight: $regular;
        margin-bottom: 0;
        line-height: 1.2;
    }
    span {
        margin-right: 2.5rem;
    }
}