.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

.seccion {
    margin: 2.5rem auto;
}

.contenido-centrado {
    max-width: 800px;
}

.btn-centrados {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 2rem auto;
    @include tablet {
        height: 50vh;
        a:last-child, a:nth-child(2),a:nth-child(3) {
            margin-top: 3rem;
        }
    }
}

.mt-1 {
    margin-top: 1rem;
}