.footer {
    background-color: $cafeOscuro;
    padding: 3rem 0;
    .contenido-footer {
        @include grid(3,2rem);
        align-items: start;
        @include tablet {
            .logo-footer {
                width: 15rem;
                margin: 0 auto;
            }
        }
    }
}

.avisos {
    display: flex;
    flex-direction: column;
    align-items: center;
    a {
        font-size: 2rem;
        color: white;
    }
}

.contenedor-contactos {
    color: white;
    h3 {
        text-align: center;
        margin: 0;
    }
    .contactos {
        @include grid(3,0.5rem);
        row-gap: 2rem;
        justify-items: center;
        a {
            max-width: 3rem;
            max-height: 3rem;
            filter: invert(100%);
        }
    }
}