.boton {
    color: white;
    font-weight: $bold;
    font-size: 2rem;
    padding: 1rem 3rem;
    margin-top: 2.5rem;
    display: block;
    text-align: center;
    border: none;
    flex: 0 0 100%;
    border-radius: 1rem;
    @include tablet {
        display: inline-block;
        flex: 0 0 auto;
    }
    &:hover {
        cursor: pointer;
    }
}

.btn-principal {
    background-color: $primNaranja;
    &:hover {
        background-color: darken($primNaranja, 5);
    }
}

.btn-secundario {
    background-color: $primNaranjaClaro;
    &:hover {
        background-color: darken($primNaranjaClaro, 5);
    }
}

.btn-gris {
    background-color: $gris;
    margin-top: 1rem;
    width: 100%;
    &:hover {
        background-color: darken($gris, 10);
    }
    @include tablet {
        width: 50%;
    }
}

.btn-naranja {
    background-color: $naranja;
    width: 100%;
    margin-top: 0.5rem;
    &:hover {
        background-color: darken($naranja, 10);
    }
    @include tablet {
        width: 50%;
    }
}